<template>
  <div class="list">
    <Loading :intSwitch=intSwitch></Loading>
    <div class="item" v-for="(item, index) in items" :key="item.id" :data-index="index" @click="toArt(item.id)">
      <div class="title">
        <span>{{item.title.rendered}}</span>
        <span class="time">{{slicedDate(item.date)}}</span>
      </div>
      <div class="content">
        {{slicedContent(item.content.rendered)}}
      </div>
    </div>
  </div>
</template>

<script>
import bus from '@/bus.js'
import Loading from '@/components/Loading.vue'
export default {
  name: 'List',
  props: {cat: {}, per_page: {}, page: {default: 1}, tag: {}},
  data () {
    return {
      items: [],
      intSwitch: 0
    }
  },
  components: {
    Loading
  },
  created () {
  },
  computed: {
    slicedContent: function () {
      return function (content) {
        let plain = content.replace(/<[^>]*>/g, '')
        return plain.length > 147 ? plain.substring(0, 147) + '...' : plain
      }
    },
    catAndPage: function () {
      return [this.cat, this.page, this.tag].join()
    },
    slicedDate: function () {
      return function (d) {
        let ds = d.split(/[-T:]/)
        ds[0] = ds[0].slice(2, 4)
        ds = ds.slice(0, -1)
        ds = ds.slice(0,3).join('-') + ' ' + ds.slice(3,5).join(':')
        return ds
      }
    }
  },
  methods: {
    toArt: function (id) {
      this.$router.push({path: 'art', query: {id: id}})
    },
    fetchData: function () {
      this.intSwitch = 1
      let query = window.ip + 'posts' + '?categories=' + this.cat + '&page=' + this.page + '&per_page=' + this.per_page
      if (this.tag != undefined) query += '&tags=' + this.tag
      fetch(query)
      .then(res => {
        return res.json()
      }).then(json => {
        this.intSwitch = 0
        this.items = json
        this.$emit('loadOK')
      })
    }
  },
  watch: {
    'catAndPage': {
      handler: function (val) {
        this.fetchData()
      },
      immediate: false,
    }
  }
}
</script>

